import React from "react";
import HomeImage from '../../assets/programming.svg'
import TwoColumn from "../TwoColumn";
const Home = () => {
    return (
        <>
            <TwoColumn
             title='Get Better with Help from RH Infotech'
             desc='What good is industry leading software if it isnt an ideal fit for your unique needs? Dont worry, we can build you the perfect solution by customizing Hubspots design and functionality, while simultaneously ensuring a smooth integration with existing tools. You ask - we deliver. Its simply better.'
             btnText='Learn More'
             image={HomeImage}
             uniqueClass='home-page'
            />
        </>
    )
}
export default Home;